
import { defineComponent, PropType } from "vue";

export default defineComponent({
  name: "Nav",
  props: {
  },
  data() {
    return { 
        Ids : [ "Home" , "Educations" , "WorkExperience" , "SideProject"]
    }
  },
  methods: {
   jumpToId( id : string ){
      const container = document.getElementById(id);
      if( container != null){
        container.scrollIntoView({ behavior: 'smooth' });
      }
    },
  },
})
